type Params = {
    id: string;
}

function getBlog(id: string) {
  return allBlogs.find((blog) => blog.id === parseInt(id));
}


function getRelatedBlogs(currentBlogId: number, allBlogs: any[]) {
    const currentBlog = allBlogs.find((blog) => blog.id === currentBlogId);

    if (!currentBlog) {
        return [];
    }

    const relatedBlogs = allBlogs
        .filter((blog) => blog.id !== currentBlogId) // Exclude the current blog
        .sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime()) // sort by latest
        .slice(0, 3);

    return relatedBlogs;
}



export default async function BlogPage({ params }: { params: Params }) { // No 'use client'
    const { id } = params;
    const blog = getBlog(id);  // Still fine, as it's synchronous
    const relatedBlogs = getRelatedBlogs(blog.id, allBlogs);

    const shareUrl = `http://localhost:3000/blogs/${id}`; //Replace with your actual domain

   const formatDate = (dateString: string) => {
        const [year, month, day] = dateString.split('-');
        return `${day}-${month}-${year}`;
    };

    if (!blog) {
        notFound();
    }
    return (
        <div className="container mx-auto px-4 md:px-8 py-12">
            <Link href="/blogs">
            <Button variant="link" className="mb-4">
                <FiArrowLeft className="mr-2" /> Back to All Blogs
                </Button>
            </Link>
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.6 }}
            >
                <h1 className="text-4xl font-bold mb-4">{blog.title}</h1>
               <p className="text-muted-foreground mb-4">Published: {formatDate(blog.date)}</p>


                {/* Image */}
                 <div className="relative w-full h-96 mb-8">
                <Image
                    src={blog.imageUrl || '/placeholder-image.jpg'} // Use a placeholder if no image
                    alt={blog.title}
                    fill
                    style={{objectFit: "cover", borderRadius: "0.5rem"}}
                    className="rounded-lg"
                />
                </div>

                {/* Blog Content (using dangerouslySetInnerHTML for rich text) */}
                <div
                className="prose prose-sm sm:prose lg:prose-lg xl:prose-xl max-w-full"
                dangerouslySetInnerHTML={{ __html: blog.content }}
                />


                {/*Client Component for Interactivity: */}
             <BlogPageClient shareUrl={shareUrl} blog={blog} />


                {/* Read More Blogs (Related Blogs) */}
               
                
            <div className="mt-12">
              
                <h2 className="text-2xl font-semibold mb-6">Read More Blogs</h2>

                <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
                 
                    {relatedBlogs.map((relatedBlog) => (
                        <Card key={relatedBlog.id}>
                        <CardHeader>
                            <CardTitle> <Link href={`/blogs/${relatedBlog.id}`}> {relatedBlog.title} </Link> </CardTitle>
                            <CardDescription>{relatedBlog.description}</CardDescription>
                        </CardHeader>
                            
                    <CardContent>
                        <div className="relative w-full h-48">
                            <Image  
                            src={relatedBlog.imageUrl || "/placeholder-image.jpg"}
                            alt={relatedBlog.title}
                            fill
                            style={{objectFit: "cover", borderRadius: "0.5rem"}}
                            className="rounded-md"
                            
                            />    
                        </div>
                    </CardContent>
                        </Card>
                    ))}
                </div>

                {/* View All Blogs CTA */}
                <div className="mt-8 text-center">
                <Link href='/blogs'>
                    <Button variant="outline">View All Blogs</Button>
                </Link>
                </div>

                {/* Footer (You can create a separate Footer component for reuse) */}
              
                <footer className="border-t mt-12">
                <div className="container mx-auto px-4 md:px-8 py-10">
                
                </div>
                    </footer>
                </div>
            </motion.div>
        </div>
    );
}

export async function generateStaticParams() {
    return allBlogs.map((blog) => ({
        id: blog.id.toString(),
    }));
}

